.header{
    width: 100%;
    position: fixed;
    z-index: 1000;
    
    transition: .3s linear;
    &::after{
        content: '';
        width: 0;
        height: 3px;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: #CE1E1E;
        transition: .5s linear;
    }
    &::before{
        content: '';
        width: 0;
        height: 3px;
        position: absolute;
        bottom: 0;
        right: 0;
        background-color: #CE1E1E;
        transition: .5s linear;
    }
}

.header__fixed{
    background-color: #fff;
    transition: .3s linear;
    &::after{
        content: '';
        width: 50%;
        height: 3px;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: #CE1E1E;
        transition: .5s linear;
    }
    &::before{
        content: '';
        width: 50%;
        height: 3px;
        position: absolute;
        bottom: 0;
        right: 0;
        background-color: #CE1E1E;
        transition: .5s linear;
    }
}

.spec{
    position: relative;
    &::before{
        position: absolute;
        bottom: 0;
        content: '';
        width: 100%;
        height: 2px;
        background-color: #353535;
        left: 0;
    }
}

.container{
    max-width: 1600px;
    margin: 0 auto;

    @media (max-width:1700px) {
        max-width: 1300px;
    }
    @media (max-width:1300px) {
        max-width: 1000px;
    }
    @media (max-width:1000px) {
        width: 100%;
    }
}

.header__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 53px;
    
    transition: .3s linear;

    @media (max-width:1000px) {
        padding: 0 20px;
    }
}

.header__container-fixed{
    margin: 20px 0;
    transition: .3s linear;
}

.header__logo{
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;
    color: #CE1E1E;
}

.nav__header{
    @media (max-width: 700px) {
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: #CE1E1E;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        z-index: 10000;
        transition: .3s linear;
    }

    &.active{
        left: 0;
    }
}

.close{
    width: 40px;
    height: 30px;
    position: relative;
    cursor: pointer;
    display: none;

    @media (max-width:700px) {
        display: block;
    }

    div{
        width: 100%;
        height: 3px;
        background-color: #CE1E1E;
        position: absolute;
        top: 14px;
        left: 0;
    }
    &::after{
        content: '';
        width: 100%;
        height: 3px;
        background-color: #CE1E1E;
        position: absolute;
        top: 0;
        left: 0;
        transition: .2s linear;
    }
    &::before{
        content: '';
        width: 100%;
        height: 3px;
        background-color: #CE1E1E;
        position: absolute;
        bottom: 0;
        left: 0;
        transition: .2s linear;
    }

    &.active{
        div{
            display: none;
        }
        &::after{
            animation: closebtn .3s linear;
            
            animation-fill-mode:forwards;
        }
        &::before{
            animation: closebtntwo .3s linear;
            animation-fill-mode:forwards;
        }

        @keyframes closebtn {
            50%{
                top: 50%;
                background-color: #fff;
            }
            100%{
                top: 50%;
                transform: rotateZ(-45deg);
            }
        }

        @keyframes closebtntwo {
            50%{
                top: 50%;
                background-color: #fff;
            }
            100%{
                top: 50%;
                transform: rotateZ(45deg);
            }
        }
    }
}

.nav__item{
    margin-right: 60px;

    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    color: #353535;
    transition: .3s linear;
    position: relative;

    &:last-child{
        margin: 0;
    }

    &::after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 2px;
        background-color: #CE1E1E;
        transition: .3s linear;
    }

    &:hover{
        color: #CE1E1E;
    }

    &:hover::after{
        width: 100%;
    }

    @media (max-width: 700px) {
        margin-right: 0;
        color: #fff;
        margin-bottom: 20px;

        &:hover{
            color: #353535;
        }
    }
}

.phone{
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;

    color: #353535;

    @media (max-width: 600px) {
        display: none;
    }
}