.main{
    position: relative;
    width: 100%;
}
.container__list{
    background-color: #fff;
    border-radius: 10px;
    padding: 80px 135px;
    position: absolute;
    top: -100px;
    left: 0;
    right: 0;
    max-width: 1340px;
    margin: 0 auto;
    z-index: 100;

    @media (max-width:1000px) {
        position: inherit;
        padding: 80px 100px;
    }
    @media (max-width:600px) {
        padding: 0;
    }
}

.container__list-two{
    max-width: 1340px;
    margin: 0 auto;
}

.header__list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 80px;

    @media (max-width:1000px) {
        justify-content: center;
        flex-direction: column;
        margin-bottom: 40px;
    }

   
}

.title__list{
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;

    letter-spacing: 0.025em;
    text-transform: uppercase;

    color: #353535;

    @media (max-width:1000px) {
        margin-bottom: 30px;
    }
    @media (max-width:600px) {
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
    }
}

.product__list{
    display: flex;
    justify-content: space-between;
    
    flex-wrap: wrap;

    @media (max-width:800px) {
        flex-direction: column;
        justify-content: center;
    }
}

.product__card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;

    
}



.product__img{
    width: 287px;
    height: 287px;
    object-fit: cover;
}

.product__title{
    margin: 43px 0 34px;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.025em;
    width: 287px;
    text-align: center;
    color: #454545;
}

.product__link{
    font-weight: 500;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.01em;
    text-decoration-line: underline;

    color: #454545;
}

.product__btn{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 26px;
    line-height: 31px;
    letter-spacing: 0.01em;
    margin-top: 98px;
    color: #CE1E1E;
}

.galery{
    margin-top: 150px;
}

.galery__list{
    display: grid;
    grid-gap: 20px;
    justify-items: center;
    align-items: stretch;
    grid-template-columns: 1fr 1fr 1fr; 
    grid-template-rows: .6fr 0.4fr 0.4fr 0.4fr; 
    gap: 50px 50px; 
    grid-template-areas: 
      "a a a"
      "c f e"
      "c d e"
      "b d g"; 
    
      @media (max-width:1000px) {
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        .galery__img:nth-child(n+2){
            display: flex;
        }
    } 
}
.galery__img{
    width: 100%;
    object-fit: cover;
    position: relative;
    top: 0;
    transition: .3s linear;
    cursor: pointer;

    &::after{
        content: '';
        background-color: #CE1E1E;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1000;
    }

    &:hover{
        background-color: #CE1E1E;
    }

    
}

.galery__img:first-child{
    grid-area: a;
}
.galery__img:nth-child(2){
    grid-area: b;
}
.galery__img:nth-child(3){
    grid-area: c;
}
.galery__img:nth-child(4){
    grid-area: d;
}
.galery__img:nth-child(5){
    grid-area: f;
}
.galery__img:nth-child(6){
    grid-area: g;
}
.galery__img:nth-child(7){
    grid-area: e;
}

.clients{
    margin-top: 150px;
}

.slider{
    margin-bottom: 50px;
    img{
        height: 100px;
    }
}

.slider-rtl{
