.specDoor__list{
    padding: 80px 135px 0 135px;

    @media (max-width:600px) {
        padding: 120px 30px 0 30px;
    }
}

.specDoor__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 150px;
    position: relative;

    .specDoorImg{
        width: 450px;
        height: 550px;
        object-fit: cover;
        margin-right: 90px;
    }
    
    .specDoor__title{
        font-weight: 600;
        font-size: 32px;
        line-height: 39px;
        letter-spacing: 0.025em;
        text-transform: uppercase;
    
        color: #353535;
        margin-bottom: 50px;

        @media (max-width:600px) {
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            letter-spacing: 0.025em;
            text-transform: uppercase;
            margin: 20px 0;
            color: #353535;
        }
    }
    
    .specDoor__text{
        font-weight: 600;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.025em;
    
        color: #454545;

        @media (max-width:600px) {
            font-weight: 500;
            font-size: 16px;
            line-height: 130.02%;

            letter-spacing: 0.01em;

            color: #454545;
        }
    }

  

    &:nth-child(2n){
        flex-direction: row-reverse;

        .specDoorImg{
            margin-right: 0;
            margin-left: 90px;
        }
    }

    @media (max-width:600px) {
        flex-direction: column;
        margin-bottom: 80px;
        &:nth-child(2n){
            flex-direction: column;
    
            .specDoorImg{
                margin-right: 0;
                margin-left: 0px;
            }
        }

        .specDoorImg{
            width: 100%;
            height: 100%;
            margin-right: 0;
            margin-left: 0;
        }
    }
}

