.header__video{
    width: 100%;
    height: 100vh;
    position: relative;
}

.video__gradient{
    background: linear-gradient(180deg, rgb(255, 255, 255) 0%, rgba(217, 217, 217, 0) 27.6%, rgba(230, 230, 230, 0) 78.12%, rgb(255, 255, 255) 100%);
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}

.video{
    width: 100%;
    height: 100%;
    overflow: hidden;
    object-fit: cover;
    position: absolute;
    z-index: -1;
}

.intro__container{
    margin: 290px 0 0 270px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;

    @media (max-width: 1000px) {
        margin: 290px 0;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
    }
}

.intro__subtitle{
    font-weight: 500;
    font-size: 30px;
    line-height: 36px;
    letter-spacing: 0.025em;

    color: #FFFFFF;
}

.intro__title{
    font-weight: 600;
    font-size: 60px;
    line-height: 73px;
    letter-spacing: 0.025em;
    text-transform: uppercase;

    color: #FFFFFF;
}

.intro__text{
    font-weight: 500;
    font-size: 24px;
    line-height: 131.02%;

    letter-spacing: 0.01em;

    color: #DFDFDF;
    margin: 62px 0 52px 0;

}

.btn{
    font-weight: 600;
    font-size: 26px;
    line-height: 31px;
    letter-spacing: 0.01em;

    color: #FFFFFF;
    background: #CE1E1E;
    border-radius: 6px;
    padding: 18px 53px;
    transition: .3s linear;
    cursor: pointer;

    &:hover{
        background-color: #9e0909;
    }
}