.modal{
    position: fixed;
    overflow: hidden;
    width: 100%;
    height: 100%;
    z-index: 100000;
    top: 0;
    left: 0;
    background-color: rgba($color: #333, $alpha: .8);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: .5s linear;

    &.active{
        opacity: 1;
        pointer-events: all;
        transition: .3s linear;
    }

}

.modal__container{
    background-color: #FFFFFF;
    padding: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 700px;
    transform: scale(1);
    transition: .4s linear;

    .btn{
        border: none;
    }

    &.active{
        opacity: 1;
        transform: scale(.9);
    }
    
    @media (max-width:600px) {
        transform: scale(.9);
        &.active{
            opacity: 1;
            transform: scale(.6);
        }
    }
}

.modal__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    width: 100%;
}

.modal__title{
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    /* identical to box height */
  
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #353535;

    @media (max-width:600px) {
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
    }
}

.modal__close{
    width: 30px;
    height: 30px;
    cursor: pointer;

    .close__x{
        width: 100%;
        height: 100%;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 3px;
            background-color: #333;
            top: 13px;
            left: 0;
            transform: rotateZ(45deg);
        }
        &::before{
            content: '';
            position: absolute;
            width: 100%;
            height: 3px;
            background-color: #333;
            top: 13px;
            left: 0;
            transform: rotateZ(-45deg);
        }
    }
}

.modal__subtitle{
    font-weight: 500;
    font-size: 24px;
    line-height: 131.02%;

    text-align: left;
    letter-spacing: 0.01em;
    width: 100%;
    color: #000000;
    margin-bottom: 40px;

    @media (max-width:600px) {
        font-weight: 500;
        font-size: 16px;
    }

}

.modal__input{
    width: 100%;

    div{
        margin-bottom: 15px;
    }
}

.modalInput__title{
    font-weight: 500;
    font-size: 24px;
    line-height: 131.02%;

    letter-spacing: 0.01em;

    color: #000000;
    margin-bottom: 5px;
}

.input__call{
    width: calc(100% - 60px);
    padding: 15px 30px;
    border: 2px solid #D9D9D9;
    border-radius: 7px;
}

.check__text{
    font-weight: 500;
    font-size: 18px;
    line-height: 131.02%;
    /* or 24px */

    letter-spacing: 0.01em;

    color: #454545;

    @media (max-width:600px) {
        font-weight: 500;
        font-size: 13px;
        line-height: 131.02%;
    }

}

.field__wrapper {
    position: relative;
    margin: 15px 0;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
   
  .field__file {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }
   
  .field__file-wrapper {
    width: 100%;
    
    display: flex;
    justify-content: space-between;
  }
   
  .field__file-fake {
    height: 60px;
    width: calc(100% - 240px);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0 30px;
    border: 2px solid #D9D9D9;
    border-radius: 7px;
    margin-right: 20px;
    position: relative;
    
  }
   
  .field__file-button {
    width: 220px;
    height: 60px;
    background: #CE1E1E;
    color: #fff;
    font-size: 1.125rem;
    font-weight: 700;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    border-radius: 6px;
    cursor: pointer;
  }

  .close__file{
    position: absolute;
    top: 15px;
    right: 255px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    z-index: 10000;
    margin-bottom: 0;

    &::after{
        width: 100%;
        height: 3px;
        background-color: #333;
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotateZ(45deg);
    }
    &::before{
        width: 100%;
        height: 3px;
        background-color: #333;
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotateZ(-45deg);
    }

    &.active{
        display: block;
    }
  }