.modalDoor{
    position: fixed;
    overflow: hidden;
    width: 100%;
    height: 100%;
    z-index: 100000;
    top: 0;
    left: 0;
    background-color: rgba($color: #333, $alpha: .8);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: .5s linear;

    &.active{
        opacity: 1;
        pointer-events: all;
        transition: .3s linear;
    }
}

.modalDoor__container{
    background-color: #FFFFFF;
    padding: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 700px;
   // transform: scale(0);
    transition: .4s linear;

    .btn{
        border: none;
    }

    &.active{
        opacity: 1;
        transform: scale(.9);
    }
}

.modal__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    width: 100%;
}

.modal__title{
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    /* identical to box height */
  
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #353535;
}

.modal__close{
    width: 30px;
    height: 30px;
    cursor: pointer;

    .close__x{
        width: 100%;
        height: 100%;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 3px;
            background-color: #333;
            top: 13px;
            left: 0;
            transform: rotateZ(45deg);
        }
        &::before{
            content: '';
            position: absolute;
            width: 100%;
            height: 3px;
            background-color: #333;
            top: 13px;
            left: 0;
            transform: rotateZ(-45deg);
        }
    }
}

.my-shuffle-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 auto;
}

.shuffle{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 auto;
    max-width: 270px;
}

.aspect__inner{
    width: 270px;
    height: 380px;
    img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

