input[type=checkbox] {
	display:none;
}

input[type=checkbox] + label {
	display:inline-block;
	margin:-2px;
	padding: 4px 12px;
	margin-bottom: 0;
	font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;

    color: #353535;
	text-align: center;
	vertical-align: middle;
	cursor: pointer;
	background-color: #fff;
	
    border: 2px solid #D9D9D9;
    border-radius: 6px;
    margin: 10px 5px;
}

 input[type=checkbox]:checked + label{
    background-image: none;
    outline: 0;
    background-color:#fff;
    border: 2px solid #CE1E1E;
    border-radius: 6px;
}

}